import axios from "axios";
import { v4 } from "uuid";

const API_URL = "https://jsonplaceholder.typicode.com/todos";

export const todoTabList = ["All", "Completed", "Uncompleted"];

const initialState = {
  todoList: [],
  currentTodoTabId: 0,
};

const actionTypes = {
  SET_TODO_LIST: "SET_TODO_LIST",
  CHANGE_TODO_COMPLETED_BY_ID: "CHANGE_TODO_COMPLETED_BY_ID",
  REMOVE_TODO_BY_ID: "REMOVE_TODO_BY_ID",
  EDIT_TODO_BY_ID: "EDIT_TODO_BY_ID",
  ADD_TODO: "ADD_TODO",
  CHANGE_TODO_TAB: "CHANGE_TODO_TAB",
};

export function getTodoList(state) {
  switch (state.currentTodoTabId) {
    case 0: {
      return state.todoList;
    }
    case 1: {
      return state.todoList.filter((todoItem) => todoItem.completed);
    }
    case 2: {
      return state.todoList.filter((todoItem) => !todoItem.completed);
    }
    default: {
      break;
    }
  }
}

export function getCurrentTodoTabId(state) {
  return state.currentTodoTabId;
}

function setTodoListAction(newTodoList) {
  return {
    type: actionTypes.SET_TODO_LIST,
    newTodoList,
  };
}

export function changeTodoCompletedById(id) {
  return {
    type: actionTypes.CHANGE_TODO_COMPLETED_BY_ID,
    id,
  };
}

export function removeTodoById(id) {
  return {
    type: actionTypes.REMOVE_TODO_BY_ID,
    id,
  };
}

export function editTodoById(id, newTitle) {
  return {
    type: actionTypes.EDIT_TODO_BY_ID,
    payload: {
      id,
      newTitle,
    },
  };
}

export function addTodo(newTitle) {
  return {
    type: actionTypes.ADD_TODO,
    newTitle,
  };
}

export function changeTodoTab(newTabId) {
  return { type: actionTypes.CHANGE_TODO_TAB, newTabId };
}

export function setTodoListAsync() {
  return async (dispatch) => {
    const tlRes = await axios.get(API_URL, { params: { userId: 1 } });

    dispatch(setTodoListAction(tlRes.data));
  };
}

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case actionTypes.SET_TODO_LIST: {
      return {
        ...state,
        todoList: action.newTodoList,
      };
    }
    case actionTypes.CHANGE_TODO_COMPLETED_BY_ID: {
      return {
        ...state,
        todoList: state.todoList.map((todoItem) => {
          if (todoItem.id === action.id) {
            todoItem.completed = !todoItem.completed;
          }

          return todoItem;
        }),
      };
    }
    case actionTypes.REMOVE_TODO_BY_ID: {
      return {
        ...state,
        todoList: state.todoList.filter(
          (todoItem) => todoItem.id !== action.id
        ),
      };
    }
    case actionTypes.EDIT_TODO_BY_ID: {
      console.log(action.payload);
      return {
        ...state,
        todoList: state.todoList.map((todoItem) => {
          if (todoItem.id === action.payload.id) {
            todoItem.title = action.payload.newTitle;
          }

          return todoItem;
        }),
      };
    }
    case actionTypes.ADD_TODO: {
      const newTodo = {
        userId: 1,
        id: v4(),
        title: action.newTitle,
        completed: false,
      };

      return { ...state, todoList: [newTodo, ...state.todoList] };
    }
    case actionTypes.CHANGE_TODO_TAB: {
      return { ...state, currentTodoTabId: action.newTabId };
    }
    default: {
      break;
    }
  }

  return state;
}
