import styles from "../styles/TodoItem.module.scss";
import clsx from "clsx";
import { useDispatch } from "react-redux";
import {
  changeTodoCompletedById,
  removeTodoById,
  editTodoById,
} from "../store/todoListReducer";
import { useState } from "react";

export default function TodoItem(props) {
  const dispatch = useDispatch();
  const [isEdit, setIsEdit] = useState(false);
  const [newTitleInput, setNewTitleInput] = useState(props.todoItem.title);

  function submitNewTitle(event) {
    event.preventDefault();

    if (newTitleInput === "") {
      alert("Please enter content.");
      return;
    }

    dispatch(editTodoById(props.todoItem.id, newTitleInput));
    setIsEdit(false);
  }

  return (
    <li>
      <div className={styles.box}>
        {isEdit ? (
          <form className={styles.formEditTodo} onSubmit={submitNewTitle}>
            <input
              autoFocus
              type="text"
              value={newTitleInput}
              onChange={(event) => setNewTitleInput(event.target.value)}
            />
          </form>
        ) : (
          <div className={styles.title}>
            <input
              type="checkbox"
              id={props.todoItem.id}
              checked={props.todoItem.completed}
              onChange={() =>
                dispatch(changeTodoCompletedById(props.todoItem.id))
              }
            />
            <label htmlFor={props.todoItem.id} className={styles.checkbox}>
              <div
                className={clsx(styles.checkmark, {
                  [styles.checkmarkChecked]: props.todoItem.completed,
                })}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width={20}
                  height={20}
                  fill="currentColor"
                  className="bi bi-check-lg"
                  viewBox="0 0 16 16"
                >
                  <path d="M12.736 3.97a.733.733 0 0 1 1.047 0c.286.289.29.756.01 1.05L7.88 12.01a.733.733 0 0 1-1.065.02L3.217 8.384a.757.757 0 0 1 0-1.06.733.733 0 0 1 1.047 0l3.052 3.093 5.4-6.425a.247.247 0 0 1 .02-.022Z" />
                </svg>
              </div>
              <p>{props.todoItem.title}</p>
            </label>
          </div>
        )}
        <div style={{ display: "flex" }}>
          {!props.todoItem.completed && (
            <div className={styles.edit} onClick={() => setIsEdit(!isEdit)}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width={20}
                height={20}
                fill="currentColor"
                className="bi bi-pen"
                viewBox="0 0 16 16"
              >
                <path d="m13.498.795.149-.149a1.207 1.207 0 1 1 1.707 1.708l-.149.148a1.5 1.5 0 0 1-.059 2.059L4.854 14.854a.5.5 0 0 1-.233.131l-4 1a.5.5 0 0 1-.606-.606l1-4a.5.5 0 0 1 .131-.232l9.642-9.642a.5.5 0 0 0-.642.056L6.854 4.854a.5.5 0 1 1-.708-.708L9.44.854A1.5 1.5 0 0 1 11.5.796a1.5 1.5 0 0 1 1.998-.001zm-.644.766a.5.5 0 0 0-.707 0L1.95 11.756l-.764 3.057 3.057-.764L14.44 3.854a.5.5 0 0 0 0-.708l-1.585-1.585z" />
              </svg>
            </div>
          )}
          <div
            className={styles.remove}
            onClick={() => dispatch(removeTodoById(props.todoItem.id))}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width={20}
              height={20}
              fill="currentColor"
              className="bi bi-trash"
              viewBox="0 0 16 16"
            >
              <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
              <path
                fillRule="evenodd"
                d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
              />
            </svg>
          </div>
        </div>
      </div>
    </li>
  );
}
