import styles from "../styles/TodoTabList.module.scss";
import { useSelector, useDispatch } from "react-redux";
import {
  getCurrentTodoTabId,
  todoTabList,
  changeTodoTab,
} from "../store/todoListReducer";
import clsx from "clsx";

export default function TodoTabList() {
  const currentTodoTabId = useSelector((state) =>
    getCurrentTodoTabId(state.todoList)
  );
  const dispatch = useDispatch();

  return (
    <ul className={styles.todoTabList} style={{ textAlign: "center" }}>
      {todoTabList.map((todoTab, index) => (
        <li key={todoTab} className={styles.todoTabItem}>
          <input
            type="radio"
            id={todoTab}
            checked={currentTodoTabId === index}
            onChange={() => dispatch(changeTodoTab(index))}
          />
          <label
            htmlFor={todoTab}
            className={clsx(styles.todoTabItemTitle, {
              [styles.todoTabItemActive]: currentTodoTabId === index,
            })}
          >
            <span>{todoTab}</span>
          </label>
        </li>
      ))}
    </ul>
  );
}
